<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process258"></a>

<p><br></p>
<p><span class="rvts17">城门之战（</span><span class="rvts50">Battle at the Gates</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:295,Y:151</span><span class="rvts21">，地点：阿克斯城门口</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">帮助圣骑士击退虚空怪</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1425.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">在刚到达阿克斯城门口的时候，会遇到圣骑士和虚空怪正在抗争，帮助圣骑士打败虚空怪。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1426.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">之后找到圣骑士队长谈话。如果之前我们在死神海岸的</span><span class="rvts20"></span><span class="rvts21">黑坑洞的黑暗交易</span><span class="rvts20"></span><span class="rvts21">支线中，成功帮助过那里的圣骑士，这里面会有一些额外的对话内容。询问她关于达利斯的下落，并完成该支线任务。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



